$color-ac-border : #acacac;

#featured-image{
  img{
    width: 100vw;
  }
}

#contact-page{
  font-size: 1.5rem;

  #content{
    padding: 3.5rem 0;
  }

  h2{
    font-size: 3.5rem;
    letter-spacing: -1px;
    margin-bottom: 3.5rem;
  }

  .contact-item{
    margin-bottom: 2rem;
  }

  #emails{
    padding-bottom: 1.5rem;
  }

  .content-block{
    padding: 0 6rem;
    padding-bottom: 5rem;
  }

  #secondary{
    border-left: 1px solid $color-ac-border;
  }


  #map{
    height: 100vh;
  }

}
