@import "bower_components/bootstrap/scss/variables";
@import "bower_components/bootstrap/scss/mixins";

$color-acc-main-nav-border: #e5e5e5;
$color-acc-main-nav-border-bottom: #e8cfc7;
$color-acc-main-nav: #000;
$color-acc-footer-bg: #191919;
$color-acc-footer: #919191;



@media only screen  and (min-width : 1824px) {
	.container{
		max-width: 1328px;
	}
}

a{
	&:hover{
		cursor: hand;
	}
}

h2{
	font-size: 3rem;
}

#site-branding{
	#site-logo{
		padding: 2rem;
		padding-bottom:1.2rem;
	}

	#logo{
		img{
			width: 6rem;
		}
	}
}

#search-form{
	margin-top: 3.5rem;
	margin-right: 3rem;
	margin-bottom: 1rem;
	width: 20rem;
	.input-group{
		@include border-radius(0.5rem);

		.input-group-addon{
			font-size: 1.6rem;
			padding-left: 2rem;
		}
		
		border: 1px solid #ededed;
		input{
			height: 3.5rem;
			font-size: 1.8rem;
			border: none;
		}

	}
}

#site-navigation{
	font-size: 1.3rem;
	line-height: 5rem;

	border-top: 1px solid $color-acc-main-nav-border;
	border-bottom: 1px solid $color-acc-main-nav-border-bottom;
	padding: 0 3.5rem;

	#main-menu{
		text-align: center;
		a{
			font-size: 2.1rem;
			font-weight: bold;
			text-transform: uppercase;
			color: $color-acc-main-nav;
			padding: 0 1rem;
			font-family: 'DinMod';
		}
	}

	#cart-links{
		a{ 
			font-size: 1.3rem; 
			color: #373a3c;

			margin-left: 2.4rem;
		}

	}
}

nav{
	ul{
		padding: 0;
		margin: 0;

		li{
			margin: 0;
			padding: 0;
		}
	}
}

.site-footer{
	padding-top: 5rem;
	background: $color-acc-footer-bg;
	min-height: 15rem;
	font-size: 1.4rem;
	color: $color-acc-footer;
	
	.site-links{
		ul{ margin: 0; padding: 0; }
		li{ margin: 0; padding: 0; list-style: none; margin-bottom: 1.8rem; }
		a{
			font-size: 1.8rem;
			color: $color-acc-footer;
			font-family: 'DinMod';
		}
	}
	.site-info{
		border-top: 1px solid #474747;
		margin-top: 4rem;
		padding: 1.8rem 0;

		a{
			color: $color-acc-footer;
			margin-left: 2.2rem;
		}
	}
}


// mobile goes right here

.collapse{
	overflow: hidden;
}

#site-mobile-navigation{

}

body.mobile{
	margin-top: 7rem;
}

#toggle-menu-button{
	font-size: 2.2rem;
	color: #000;
	text-decoration: none;
	&:hover{
		text-decoration: none;
	}
}