@font-face {
    font-family: 'DinMod';
    src: url('../fonts/DinMod.eot');
    src: url('../fonts/DinMod.eot') format('embedded-opentype'),
         url('../fonts/DinMod.woff2') format('woff2'),
         url('../fonts/DinMod.woff') format('woff'),
         url('../fonts/DinMod.ttf') format('truetype'),
         url('../fonts/DinMod.svg#DinMod') format('svg');
}
